Estambul. Pero en el ao 395, el emperador Teodosio dividi el imperio, separando el mundo latino del griego. Luego de enfrentar la invasin germnica, solamente las provincias de oriente continuaron el legado cultural romano, fundindose con la cultura helenstica. En ese momento, el imperio era teocrtico, con un solo Dios y una sola autoridad, el emperador, quien estaba encargado de defender y propagar la fe; sin embargo, la divisin del imperio produjo tambin la divisin religiosa: en oriente, eran ortodoxos y en occidente, cristianos. Esta situacin produjo tambin cambios en el arte, debido a su ubicacin privilegiada entre Europa y Oriente.")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/29.jpg"),"alt":"Texto que describa la imagen"}})])])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('h4',[_vm._v("El arte bizantino")]),_c('p',[_vm._v("La pintura en este periodo representa el absoluto influjo de la religin en la vida social y poltica del Imperio Oriental, pero a comienzos del ao 730, bajo la autoridad del emperador Len III, surgi una crisis iconoclasta que dur hasta el siglo IX, cuando se prohbe la elaboracin de imgenes religiosas en el Imperio, ya que se consider que dichas imgenes conducan a la idolatra, porque en la materia estaba lo maligno y, por lo tanto, no contribuan en el propsito de seguir el camino a lo divino. Por esa razn, los mosaicos fueron cubiertos dentro de los templos, los motivos religiosos se reemplazaron por retratos del emperador, escenas de guerra, de carreras de caballos o de caza, vegetales o animales."),_c('br'),_c('br'),_vm._v("Luego vino el cisma entre las iglesias de Constantinopla y Roma, entonces las imgenes volvieron a ser admitidas en los templos, pero con un valor sagrado, rindiendo culto a Dios por medio de ellas.")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/30.jpg"),"alt":"Texto que describa