 Las imgenes tienen como protagonista a Cristo. Utilizaron la decoracin geomtrica, vegetal, humana o animal, dispuesta en sentido radial. En las columnas tambin esculpieron estatuas; las representaciones, en general, eran religiosas, relacionadas con diferentes pasajes de la Biblia, por ejemplo, los cuatro evangelistas, el juicio final y otros. La ubicacin de las imgenes dentro de las iglesias se haca por orden jerrquico y de importancia, por ejemplo, en el norte est el antiguo testamento, el sur es el nuevo y el oeste el juicio final. Las pinturas murales se ubicaron en el bside o parte posterior del altar. ")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/32.jpg"),"alt":"Texto que describa la imagen"}})])])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('h4',[_vm._v("El arte gtico")]),_c('p',[_vm._v("Se inici en el siglo XII, alcanzando su esplendor en los siglos XIII al XV. Los cambios en la sociedad produjeron el desplazamiento de los campos hacia las regiones urbanas, donde domin la burguesa, clase fundamentalmente comerciante, que propendi por el sentido comunal, la defensa de los intereses de sus habitantes y la autonoma municipal. Por esa razn, al lado del poder religioso representado en las catedrales, se construyeron los ayuntamientos. Los artesanos y los artistas se organizaron en gremios que buscaban evitar la competencia y el monopolio. Los gremios se encargaron de realizar numerosas obras artsticas, siendo la ciudad un polo de desarrollo cultural, sobre todo por el surgimiento de las universidades, que desplazaron a los monasterios. en la arquitectura gtica se utiliz el vidrio con tonos preferencialmente azules y rojos, y la piedra en forma de sillares, por cuanto lo estructural tuvo para la poca enorme valor esttico.")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/33.jpg"),"alt":"Texto que describa la imagen"}})]