      h2 1.2 Historia del arte
    p.mb-5 La historia del arte <b>es la disciplina de las ciencias sociales encargada de estudiar la evolucin del arte a travs de cada etapa del tiempo</b>, basando sus estudios en la clasificacin y observacin de culturas, pueblos y civilizaciones. Su campo de exploracin permite identificar cmo el hombre ha evolucionado a travs de los tiempos y cul era su visin respecto al mundo que lo rodeaba en cada etapa desde su existencia. Su estudio requiere ayuda de otras mltiples disciplinas, como arqueologa, antropologa, geografa, historia y otras ms. 
