    SlyderE(indicadores="izquierda").mb-5 
      .row
        .col-md-6.mb-4.mb-md-0
          h4 El arte bizantino
          p La palabra bizantino corresponde al Imperio Romano de oriente, cuya capital era Bizancio, en el periodo comprendido entre el 395 y el 1454, ao en que Constantinopla fue tomada por los turcos. El emperador Constantino pretendi consolidar la unidad poltica, trasladando la capital imperial de Roma a Bizancio, que era una ciudad griega en los lmites entre Europa y Asia. Hoy se conoce como Estambul. Pero en el ao 395, el emperador Teodosio dividi el imperio, separando el mundo latino del griego. Luego de enfrentar la invasin germnica, solamente las provincias de oriente continuaron el legado cultural romano, fundindose con la cultura helenstica. En ese momento, el imperio era teocrtico, con un solo Dios y una sola autoridad, el emperador, quien estaba encargado de defender y propagar la fe; sin embargo, la divisin del imperio produjo tambin la divisin religiosa: en oriente, eran ortodoxos y en occidente, cristianos. Esta situacin produjo tambin cambios en el arte, debido a su ubicacin privilegiada entre Europa y Oriente.
      
        .col-md-6
          figure
            img(src='@/assets/curso/29.jpg', alt='Texto que describa la imagen')
      .row
        .col-md-6.mb-4.mb-md-0
          h4 El arte bizantino
          p La pintura en este periodo representa el absoluto influjo de la religin en la vida social y poltica del Imperio Oriental, pero a comienzos del ao 730, bajo la autoridad del emperador Len III, surgi una crisis iconoclasta que dur hasta el siglo IX, cuando se prohbe la elaboracin de imgenes religiosas en el Imperio, ya que se consider que dichas imgenes conducan a la idolatra, porque en la materia estaba lo maligno y, por lo tanto, no contribuan en el propsito de seguir el camino a lo divino. Por esa razn, los mosaicos fueron cubiertos dentro de los templos, los motivos religiosos se reemplazaron por retratos del emperador, escenas de guerra, de carreras de caballos o de caza, vegetales o animales.
            br
            br
            |Luego vino el cisma entre las iglesias de Constantinopla y Roma, entonces las imgenes volvieron a ser admitidas en los templos, pero con un valor sagrado, rindiendo culto a Dios por medio de ellas.
      
        .col-md-6
          figure
            img(src='@/assets/curso/30.jpg', alt='Texto que describa la imagen')
      .row
        .col-md-6.mb-4.mb-md-0
          h4 El arte romnico
          p Surge en el siglo X y se extiende hasta el XII. En el siglo X, el emperador Otn, junto con Carlomagno, quien gobern en el siglo IX, fij las bases de la Europa feudal, caracterizada por el feudo y la dependencia de los vasallos respecto a su seor. En el centro de la propiedad, que era una enorme extensin de tierra, estaba el castillo, la catedral o la abada. El poder dependa de la cantidad de tierras que se poseyera, y en la cspide social estaba el rey. En esa poca, la iglesia catlica logr la unidad de Europa; pero la Iglesia estaba manejada por el poder civil, pues los obispos eran nobles a quienes los reyes u otros nobles les entregaban sus dicesis; en correspondencia, la institucin del papado se fortaleci. Los monjes se dedicaron a realizar trabajos manuales, tareas agrcolas y artesanales, convirtindose los monasterios benedictinos en centros econmicos independientes y en focos culturales de primera importancia, ya que copiaban manuscritos antiguos, realizaban pinturas de miniaturas y redactaban crnicas histricas. 
      
        .col-md-6
          figure
            img(src='@/assets/curso/31.jpg', alt='Texto que describa la imagen')
      .row
        .col-md-6.mb-4.mb-md-0
          h4 El arte romnico
          p En cuanto a la arquitectura romnica, se caracteriza por las iglesias en forma de cruz latina; usualmente, tienen una cripta en la que se depositaban los cuerpos de los santos. En esta poca, se retomaron materiales y tcnicas de construccin de los romanos, tambin se construyeron campanarios. La arquitectura tena un significado religioso, por eso la escultura romnica es monumental, y estas obras, como iban dirigidas a los fieles y los peregrinos, se ubicaban en las portadas de las iglesias, dentro de ellas y en los conventos. Las imgenes tienen como protagonista a Cristo. Utilizaron la decoracin geomtrica, vegetal, humana o animal, dispuesta en sentido radial. En las columnas tambin esculpieron estatuas; las representaciones, en general, eran religiosas, relacionadas con diferentes pasajes de la Biblia, por ejemplo, los cuatro evangelistas, el juicio final y otros. La ubicacin de las imgenes dentro de las iglesias se haca por orden jerrquico y de importancia, por ejemplo, en el norte est el antiguo testamento, el sur es el nuevo y el oeste el juicio final. Las pinturas murales se ubicaron en el bside o parte posterior del altar. 
      
        .col-md-6
          figure
            img(src='@/assets/curso/32.jpg', alt='Texto que describa la imagen')
      .row
        .col-md-6.mb-4.mb-md-0
          h4 El arte gtico
          p Se inici en el siglo XII, alcanzando su esplendor en los siglos XIII al XV. Los cambios en la sociedad produjeron el desplazamiento de los campos hacia las regiones urbanas, donde domin la burguesa, clase fundamentalmente comerciante, que propendi por el sentido comunal, la defensa de los intereses de sus habitantes y la autonoma municipal. Por esa razn, al lado del poder religioso representado en las catedrales, se construyeron los ayuntamientos. Los artesanos y los artistas se organizaron en gremios que buscaban evitar la competencia y el monopolio. Los gremios se encargaron de realizar numerosas obras artsticas, siendo la ciudad un polo de desarrollo cultural, sobre todo por el surgimiento de las universidades, que desplazaron a los monasterios. en la arquitectura gtica se utiliz el vidrio con tonos preferencialmente azules y rojos, y la piedra en forma de sillares, por cuanto lo estructural tuvo para la poca enorme valor esttico.
      
        .col-md-6
          figure
            img(src='@/assets/curso/33.jpg', alt='Texto que describa la imagen')
      .row
        .col-md-6.mb-4.mb-md-0
          h4 El arte gtico
          p Hasta el siglo XIV en Italia y en el siglo XV en Flandes, la pintura se desarroll ms tarde que la arquitectura y la escultura. En Italia, la pintura tena influencia bizantina, manteniendo un estilo idealista, con curvas delicadas, fondos dorados y cuerpos alargados, conservando la mayor parte de los descubrimientos helensticos que pasaron a la edad media. Pero las demandas de la burguesa adinerada exigan ms obras pictricas realistas, en las que se reprodujera el mundo visible aplicando el naturalismo de los escultores gticos. Pintaron especialmente figuras en un espacio que representara profundidad. Giotto utiliz el fresco con dominio completo, introdujo la tercera dimensin, lo que consigui a travs del escorzo y el juego entre luces y sombras, y el fondo antes dorado lo sustituy por el cielo y el paisaje. Giotto se hizo famoso, hecho que era nuevo, pues hasta el momento las obras de arte eran annimas, y se convirti en el precedente de la perspectiva, que fue la gran conquista del Renacimiento. 
      
        .col-md-6
          figure
            img(src='@/assets/curso/34.jpg', alt='Texto que describa la imagen')
      .row
        .col-md-6.mb-4.mb-md-0
          h4 El arte gtico
          p En cuanto a Flandes, se produjo una renovacin en la pintura, inclinndose hacia la realidad partiendo de la observacin, incorporando todos los detalles posibles, utilizando el claroscuro, lo cual hizo ganar en profundidad y volumen a las obras, dando mayor apariencia de vida. Utilizaron el leo, cubierto al final con una capa de barniz. Esta tcnica pictrica permite desarrollar el estilo flamenco, pintando imgenes religiosas y el retrato de los burgueses flamencos, quienes preferan pinturas en caballete por ser ms fciles de transportar. En ese momento histrico, Italia ya se encaminaba al Renacimiento.
      
        .col-md-6
          figure
            img(src='@/assets/curso/35.jpg', alt='Texto que describa la imagen')
    .texto-adorno.bg--2.px-4.py-2.mb-5(data-aos="fade-right")
      h4.mb-0.me-3 Edad Moderna
    h5(data-aos="fade-right") Renacimiento
    .row.mb-5.justify-content-center
      .col-lg-10
        .bloque-texto-g.color-primario.p-3.p-sm-4.p-md-5(data-aos="zoom-in-up")
          .bloque-texto-g__img(
            :style="{'background-image': `url(${require('@/assets/curso/36.jpg')})`}"
          )
          .bloque-texto-g__texto.p-4
            p.mb-0 Fue el nombre que se le dio a un importante movimiento filosfico y artstico que se produjo en Europa desde finales del siglo XIV hasta el siglo XVI, y fue una etapa de transicin de la Edad Media a la Era Moderna. Su principal desarrollo se produjo en Italia. Su denominacin se debe a que en esta poca se redescubrieron la cultura greco-romana y la filosofa clsica. Se inspir en la olvidada cultura de la Edad Media, el racionalismo, la experimentacin, y tambin en la exaltacin de la naturaleza humana, pues el hombre fue el centro del universo, la medida de todas las cosas. 
    p.mb-5 A medida que ms personas aprendan a leer y que los libros se imprimieron y llegaron a sitios geogrficos diversos, gracias al invento de la <b>imprenta, creacin de Johannes Gutenberg,</b> la capacidad de raciocinio gener una revolucin intelectual en la bsqueda del conocimiento, pues la difusin de textos propenda por la renovacin cultural y por la reinterpretacin de los valores tradicionales greco-romanos. Los cambios se dieron gradualmente y afectaron a toda la sociedad en todos los aspectos. 
      br
      br
      |Durante el Renacimiento, los artistas firmaron sus obras y la burguesa compraba imgenes o estatuas de s mismos, formando parte del hedonismo que caracteriz la poca. Los artistas eran realistas, porque integraban al hombre con la naturaleza, la creacin y el universo, y dependan econmicamente de mecenas (patrocinio que se otorgaba a los artistas), quienes eran comerciantes, banqueros, aristcratas, incluso Papas.
    .row.mb-5.justify-content-center.g-0.align-items-center
      .col-lg-5(data-aos="fade-right")
        .imagen-adornos-flotantes
          img(src="@/assets/curso/37.jpg" alt="Adornos flotantes")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float5.png" style="left: ; right: 10% ; top: ; bottom: 10% ; width: 90px ")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float5.png" style="left: 10% ; right: ; top:0 ; bottom: ; width: 60px ")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float3.png" style="left: 15% ; right: ; top: ; bottom: 5% ; width: 60px ")
      .col-lg-7(data-aos="fade-left")
        p.ms-5 El auge de El Renacimiento fue entre 1490 y 1527, y en cuanto a la arquitectura, Filippo di Ser Brunellesco Lapi fue el ms destacado, tambin fue escultor y orfebre. Otro destacado representante del Renacimiento fue Michelangelo Buonarroti, quien era arquitecto, escultor y pintor, caracterizado por su perfeccionismo en la escultura y en la pintura (realiz la obra excepcional de la bveda de la Capilla Sixtina por imposicin del Papa Julio II), elabor esculturas muy importantes, como La Piedad y El David. Asimismo, <b>Leonardo Da Vinci fue considerado el ltimo hombre de El Renacimiento</b>, fue un gran polmata (persona que se destaca en diferentes disciplinas), tuvo creaciones relacionadas con el helicptero, el submarino, entre otras. Sus obras ms representativas son La Gioconda y La ltima Cena.
        .bg--1-claro.p-3.ps-5
          p.mb-0 <b>Retrato de Lisa Gherardini</b>, esposa de Francesco del Giocondo, obra conocida como La Gioconda o La Mona Lisa. Actualmente, expuesta en el Museo del Louvre en Pars.
    h4(data-aos="fade-right") El Barroco
    .row.mb-5.justify-content-center
      .col-lg-5(data-aos="fade-right")
        .imagen-adornos-flotantes
          img(src="@/assets/curso/38.jpg" alt="Adornos flotantes")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float5.png" style="left: ; right: 10% ; top: ; bottom: 10% ; width: 90px ")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float5.png" style="left: 10% ; right: ; top:0 ; bottom: ; width: 60px ")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float3.png" style="left: 15% ; right: ; top: ; bottom: 5% ; width: 60px ")
      .col-lg-7(data-aos="fade-left")
        p Expresaba una sensibilidad desengaada, crtica, compleja y pesimista. El trmino fue utilizado inicialmente para referirse a algo engaoso o recargado, en forma despectiva, pero desde el siglo XIX, el trmino se utiliz para referirse no slo al periodo, sino a las manifestaciones artsticas que contradijeron los valores del clasicismo, mediante una negacin simblica y artstica de lo clsico, por eso fue femenino, irracional y dionisaco. El Barroco enfatiz lo cotidiano y efmero de la vida, lo cual produjo una vulgarizacin del imaginario religioso; consider que lo nacional y religioso eran expresiones de poder poltico, por eso se elaboraron obras monumentales y recargadas, que propendan por hacer resaltar el poder dominante, pero mediante engaos y artificios.
    .row.mb-5
      .col-lg-8(data-aos="fade-right")
        .bg--1-claro.p-4.mb-4
          p.mb-0 La pintura fue una expresin artstica de El Barroco. Se distinguen dos vertientes, una naturalista, cuyo estilo se basa en la observacin de la naturaleza, y la otra es el clasicismo, que es opuesta a la anterior, con un estilo ms realista y racional, predominando el dibujo sobre el color; vertientes caracterizadas por el realismo superficial, los colores ricos, y el contraste entre las luces y las sombras. Exponentes de este perodo son Rembrandt, Rubens, Velsquez, Poussin, Vermeer y Caravaggio, quien realizaba una minuciosa observacin anatmica con el uso del claroscuro, lo que otorgaba dramatismo a su obra y que lleg a conocerse como tenebrismo. Caravaggio plasm vvidos momentos y escenas relacionadas con luchas violentas, muerte o tortura.
      .col-lg-4(data-aos="fade-left"): img(src="@/assets/curso/39.jpg" alt="Adornos flotantes")
    .row.mb-5.justify-content-center.align-items-center
      .col-lg-5(data-aos="fade-right")
        .imagen-adornos-flotantes
          img(src="@/assets/curso/40.jpg" alt="Adornos flotantes")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float5.png" style="left: ; right: 10% ; top: ; bottom: 10% ; width: 90px ")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float5.png" style="left: 10% ; right: ; top:0 ; bottom: ; width: 60px ")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float3.png" style="left: 15% ; right: ; top: ; bottom: 5% ; width: 60px ")
      .col-lg-7(data-aos="fade-left")
        p La escultura barroca estuvo vinculada con la religin y con la arquitectura, porque decoraban los espacios pblicos, los palacios y las locaciones religiosas. Eran dinmicas, expresivas y ornamentadas; expresaban pasiones y estaban recargadas de detalles. Se expres en dos vertientes, una, la gtica, y otra, la renacentista. En trminos generales, durante el perodo barroco, se siguieron trabajando los temas de historia, mitologa y religin, aunque preferan las obras llenas de dramatismo. La Iglesia Catlica prefiri los temas como la inmaculada concepcin, la virgen, la eucarista, los santos, asuntos claves en la disputa del protestantismo.
    .texto-adorno.bg--2.px-4.py-2.mb-5(data-aos="fade-right")
      h4.mb-0.me-3 Edad Contempornea
    p.mb-5 Los avances tecnolgicos que se aportaron en este poca fueron de mucha trascendencia para la industria grfica; los diferentes movimientos artsticos junto a sus representantes generaron la ruptura con la pintura tradicional, por lo que es importante que revise el siguiente video, pues le permitir profundizar sobre el diseo grfico que se marc en esta poca.
    .row.mb-5.justify-content-center.align-items-center
      .col-lg-5(data-aos="fade-right")
        .imagen-adornos-flotantes
          img(src="@/assets/curso/42.jpg" alt="Adornos flotantes")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float5.png" style="left: ; right: 10% ; top: ; bottom: 10% ; width: 90px ")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float5.png" style="left: 10% ; right: ; top:0 ; bottom: ; width: 60px ")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float3.png" style="left: 15% ; right: ; top: ; bottom: 5% ; width: 60px ")
      .col-lg-7(data-aos="fade-left")
        p Adicionalmente, es importante que sepa que el cubismo tambin se desarroll en la escultura, con tcnicas parecidas al collage del cubismo sinttico, que integr seales y fragmentos de cosas reales. Se elabor con materiales de desecho, compuestas de diversas piezas, con huecos y vacos en la superficie. En la arquitectura, el cubismo no tuvo casi representacin, se realiz sobre todo en Checoslovaquia, como en la Casa de la Virgen Negra de Praga.
    h4(data-aos="fade-right") Impresionismo
    .row.mb-5.justify-content-center.align-items-center
      .col-lg-5(data-aos="fade-right")
        .imagen-adornos-flotantes
          img(src="@/assets/curso/43.jpg" alt="Adornos flotantes")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float5.png" style="left: ; right: 10% ; top: ; bottom: 10% ; width: 90px ")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float5.png" style="left: 10% ; right: ; top:0 ; bottom: ; width: 60px ")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float3.png" style="left: 15% ; right: ; top: ; bottom: 5% ; width: 60px ")
      .col-lg-7(data-aos="fade-left")
        p Este movimiento artstico se desarroll a partir de la segunda mitad del siglo XIX en Europa, especialmente en Normanda. Propenda por plasmar la luz con la impresin visual en un instante determinado, gracias a la evolucin y creacin de nuevos pigmentos, que generaron gran variedad de colores, y como, adems, los leos venan en tubos, eso impeda que se secaran cuando pintaban al aire libre. Los pintores usaron colores puros, dando origen a la ley del contraste cromtico, ya que todo color es relativo a los colores que le rodean, y a la ley de los colores complementarios. Contrastaron la luz con la oscuridad, generando la ilusin de profundidad por medio del modelado de color, que consiste en que por medio del color se crea volumen, con una zona de luz, de media sombra y sombra ms profunda, agregando blanco para la luz y negro para la sombra.
    .bg--4-claro.p-4(data-aos="zoom-in-up").mb-5
      .row.justify-content-center.align-items-center
        .col-lg-2.d-none.d-xl-block: img(src="@/assets/curso/44.svg") 
        .col-lg-8: p.mb-0 Para los pintores impresionistas, el dibujo y la forma pasaban a un segundo plano, siendo el reto ms importante plasmar la iluminacin y el color sobre el cuerpo o el objeto. Por eso, muchas imgenes se ven como diluidas o mezcladas. Buscaron iluminacin interior, exterior, iluminacin natural filtrada o al aire libre, con los reflejos sobre el agua, o la luz del sol a travs de las hojas de los rboles.  
    h4(data-aos="fade-right") Neoclasicismo
    .row.mb-5.justify-content-center.align-items-center
      .col-lg-5(data-aos="fade-right")
        .imagen-adornos-flotantes
          img(src="@/assets/curso/45.jpg" alt="Adornos flotantes")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float5.png" style="left: ; right: 10% ; top: ; bottom: 10% ; width: 90px ")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float5.png" style="left: 10% ; right: ; top:0 ; bottom: ; width: 60px ")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float3.png" style="left: 15% ; right: ; top: ; bottom: 5% ; width: 60px ")
      .col-lg-7(data-aos="fade-left")
        p Movimiento artstico con races filosficas y estticas de la antigedad clsica, movimiento moralizante, social e intelectual, para adaptarse a la modernidad y reinterpretar sus formas, que surgi a mediados del siglo XVIII y se extendi hasta el XIX. Su furor se produjo debido al descubrimiento de Pompeya y Herculano, por lo que se realizaron numerosas expediciones para conocer las obras antiguas en sus lugares de origen. Adems, apareci la Ilustracin o el Iluminismo, que defenda la razn y el conocimiento para fomentar el progreso. Igualmente, tuvo lugar la Revolucin Industrial, que modific los modos de produccin y organizacin social, y la Revolucin Francesa, con sus proclamas de igualdad, libertad y fraternidad. 
    .bg--4-claro.p-4(data-aos="zoom-in-up").mb-5
      p.mb-0 El arte de este periodo tom como temas modelos ideas morales de la historia grecolatina, la historia de las repblicas y de la Revolucin Francesa; elaboraron retratos y algunas pinturas religiosas. En cuanto a la pintura, el dibujo era de vital importancia, se us el fresco, pero primordialmente el leo; supresin de objetos decorativos. En cuanto a la anatoma, se utiliz lo impuesto por los clsicos antiguos, las pinceladas no se distinguen en las obras, la luz es clara y fra.
    .row.mb-5.justify-content-center.align-items-center
      .col-lg-5(data-aos="fade-right")
        .imagen-adornos-flotantes
          img(src="@/assets/curso/46.jpg" alt="Adornos flotantes")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float5.png" style="left: ; right: 10% ; top: ; bottom: 10% ; width: 90px ")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float5.png" style="left: 10% ; right: ; top:0 ; bottom: ; width: 60px ")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float3.png" style="left: 15% ; right: ; top: ; bottom: 5% ; width: 60px ")
      .col-lg-7(data-aos="fade-left")
        p En cuanto a la escultura, se prefiri el mrmol, el alabastro y el cobre, exaltando la belleza ideal y el equilibrio conforme a los clsicos antiguos; se dej a un lado la policroma, que consista en el arte de pintar o decorar con varios colores las obras; la representacin de las emociones era sutil; las esculturas no son dinmicas.
    .tarjeta.bg--2-claro.p-4.mb-5.rounded-0.mt-5
          .row.justify-content-around.align-items-center.position-relative
            img.position-absolute.d-none.d-lg-block(src="@/assets/curso/47.svg" alt="Adornos flotantes" style="width: 117px ; left: 3% ; top: -80% ")
            .col-4.col-sm-2.col-lg-1
            .col-lg-10
              .row.justify-content-between.align-items-center
                .col.mb-3.mb-sm-0
                  p.text-small.mb-0 Para  reforzar sus conceptos en este tema, se le invita a ver el video <b>Historia del Arte Universal</b> que se encuentra en el material complementario.
                .col-sm-auto
                  a.boton.color-acento-contenido.tb(href='https://www.youtube.com/watch?v=I7gIIby02aw&t=8s' target='_blank')
                    span Enlace
                    i.fas.fa-link
    #t_1_3.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.3 Exponentes, movimientos y tendencias del diseo
    p.mb-5 Existen diversas tendencias y exponentes de cada periodo. Es importante aclarar que un mismo artista puede pertenecer a varias tendencias, ya que muchas de ellas se han dado en cortos periodos de tiempo.
      br
      br
      |Algunos de las tendencias del diseo son: 
    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta bg--4-claro")
      .row(titulo="Art Nouveau").p-3
        .col-md-8.mb-4.mb-md-0
          p <b>Finales del siglo XIX, principios del siglo XX</b>
            br
            br
            | Este movimiento se caracteriza por tener grandes artistas en su lista. Uno de sus mayores expositores es Alfons Mucha, quien revolucion con <b>su obra, la cual representaba la belleza y erotismo de la mujer, con gran delicadeza y sofisticacin</b>. Mucha salta a la fama con la invencin de un cartel para la obra Gismonda, protagonizada por la actriz Sarah Bernhardt, actriz de cine y teatro, muy importante en la movida parisina. Sarah fue la musa de varios de sus trabajos.
            br
            br
            |Este movimiento nace bajo las formas poticas de la naturaleza, el manejo enriquecido de la lnea, las curvas, fuerza y movimiento que pueden aportar, adaptndose a la vida moderna. Al igual que muchos otros movimientos, el Art Nouveau imprime su sello en todas las reas artsticas de la poca.
            br
            br
          h4 Caractersticas
          ol.lista-ol--cuadro--color
            li 
              .lista-ol--cuadro__vineta
                span 1
              | nfasis en elementos de la naturaleza, un estilo joven y moderno.
            li 
              .lista-ol--cuadro__vineta
                span 2
              | Manejo de la lnea y las formas naturales.
            li 
              .lista-ol--cuadro__vineta
                span 3
              | Es el primer movimiento que trata de no parecerse a otro.
            li 
              .lista-ol--cuadro__vineta
                span 4
              | Se utilizaron gran cantidad de materiales, sobre todo en la arquitectura y el diseo industrial.
            li 
              .lista-ol--cuadro__vineta
                span 5
              | nfasis rico en el color.
        .col-md-4
          figure
            img(src='@/assets/curso/48.jpg', alt='Texto que describa la imagen')
      .row(titulo="Constructivismo").p-3
        .col-md-8.mb-4.mb-md-0
          p <b>(1914) Rusia</b>
            br
            | El Constructivismo nace en Rusia, en 1914. En el ao 1917, Malevich emple el trmino 
            br
            |<b><em>construction art</em></b> (en espaol, arte para construccin), haciendo referencia al trabajo de Rdchenko, 
            br
            |bajo una connotacin negativa.
            br
            br
            |Una de las obras ms icnicas de este periodo es realizada por Vladimir Tatlin, pintor y escultor, al 
            br
            |cual se asocia con el nacimiento del Constructivismo. Diseo del Monumento a la Tercera 
            br
            |Internacional, o tambin llamada Torre de Tatlin, que es un edificio habitable, ms alto que la propia 
            br
            |Torre Eiffel, el cual no se edific.
            br
            br
            |Entre sus principales exponentes, adems de Vladimir Tatlin, estn: Aleksandr Rdchenko, 
            br
            |Aleksandra Ekster, Jnos Mttis-Teutsch, Lajos Kassk, El Lissitzky, entre otros. 
          h4 Caractersticas
          ol.lista-ol--cuadro--color
            li 
              .lista-ol--cuadro__vineta
                span 1
              | Se basa en la abstraccin geomtrica y funcional.
            li 
              .lista-ol--cuadro__vineta
                span 2
              | Sus fuentes de inspiracin son el cubismo y el dadasmo.
            li 
              .lista-ol--cuadro__vineta
                span 3
              | Se utilizan colores como rojo, naranja, azul, blanco, negro y amarillo.
            li 
              .lista-ol--cuadro__vineta
                span 4
              | Rechaza el arte burgus de la poca. 
            li 
              .lista-ol--cuadro__vineta
                span 5
              | Se asocia a la produccin industrial.
          p Se utilizan tipografas en negritas en los carteles, sobre todo en la propaganda poltica.
        .col-md-4
          figure
            img(src='@/assets/curso/49.jpg', alt='Texto que describa la imagen')
      .row(titulo="Bauhaus").p-3
        .col-md-8.mb-4.mb-md-0
          p <b>Casa de la construccin estatal, 1919, Alemania.</b>
            br
            br
            |Fundada en 1919 por Walter Gropius, en Weimar, Alemania. La primera fase (1919-1923) fue idealista y romntica; la segunda (1923-1925), mucho ms racionalista; y en la tercera (1925-1929), alcanz su mayor reconocimiento, coincidiendo con su traslado de la ciudad de Weimar a Dessau (Dez y Parra, 2015).
            br
            br
            |Su caracterstica principal de diseo es la sustraccin de ornamentos como los utilizados en el Art Nouveau, de formas limpias y geomtricas, colores primarios que buscan la funcionalidad con la menor cantidad de elementos. Esta escuela es cerrada por el Partido Nacional Socialista, bajo el mandato de Hitler.
            br
            br
            |Sus principales exponentes son: Walter Gropius, Paul Josef Albers, Lyonel Feininger, Alfred Arndt, Herbert Bayer, Ludwig Hilberseimer, Marianne Brandt, Johannes Itten, Marcel Breuer, Wassily Kandinsky.
          h4 Caractersticas
          ol.lista-ol--cuadro--color
            li 
              .lista-ol--cuadro__vineta
                span 1
              | Su principal funcin es la innovacin en el pensamiento, dejando atrs los convencionalismos del momento, la forma sigue a la funcin.
            li 
              .lista-ol--cuadro__vineta
                span 2
              | La Bauhaus hizo aportes importantes al diseo grfico e industrial que se utilizan hasta el da de hoy, basados en las tendencias modernistas del uso y relacin entre el diseo, materiales funcionales y minimalistas.
            li 
              .lista-ol--cuadro__vineta
                span 3
              | El diseo tipogrfico se enriquece con el aporte de nuevos estilos, que apuestan por la funcionalidad ms que por la esttica.
            li 
              .lista-ol--cuadro__vineta
                span 4
              |  Los colores predominantes son los primarios: amarillo, azul y rojo, en contraste con formas bsicas como el tringulo, el crculo y el cuadrado.
            li 
              .lista-ol--cuadro__vineta
                span 5
              |  Las formas geomtricas y abstractas se utilizan para simplificar los elementos.
        .col-md-4
          figure
            img(src='@/assets/curso/50.png', alt='Texto que describa la imagen')
      .row(titulo="Art Dco").p-3
        .col-md-8.mb-4.mb-md-0
          p <b>Europa 1920  1939</b>
            br
            br
            |A la par que en Alemania se desarrolla la escuela de la Bauhaus, Francia y otros pases adoptan un movimiento diferente, llamado Art Dco, movimiento modernista, que se sita con una visin influenciada por diversas estticas, como la del constructivismo ruso, del cual toma las lneas duras y la solidez de las formas.
            br
            br
            |Materiales como aluminio, acero inoxidable, piel de animales, maderas y otros son los mayormente utilizados. En arquitectura, uno de los mejores ejemplos de este estilo es la cpula del edificio Chrysler, en la ciudad de Nueva York, construido entre 1928 y 1930; en este, se observan el uso de las lneas, la repeticin, y formas caractersticas del movimiento, los diversos materiales enfocados hacia un concepto industrializado.
            br
            br
            |Esta corriente busca un nuevo optimismo despus de vivir la Primera Guerra Mundial, sus diseos imprimen un toque elegante y suntuoso, encaminado hacia un futuro esperanzador. Los colores son vibrantes e irradian mucho brillo, esto como influencia del Fauvismo, las tipografas realizadas en el Art Dco, eran revestidas en muchos casos por una capa de oro, lo cual imprima un toque de suntuosidad.
          h4 Caractersticas
          ol.lista-ol--cuadro--color
            li 
              .lista-ol--cuadro__vineta
                span 1
              | Es un movimiento de posguerra, basado en el lujo y la suntuosidad, que desea dejar atrs las carencias de la poca. 
            li 
              .lista-ol--cuadro__vineta
                span 2
              | Las piezas de este movimiento son puramente decorativas, en la mayora de los casos.
            li 
              .lista-ol--cuadro__vineta
                span 3
              | Gira alrededor del progreso, el urbanismo, la ciudad y la maquinaria. 
            li 
              .lista-ol--cuadro__vineta
                span 4
              | Uso continuo de colores, brillos, lneas y formas aerodinmicas. 
        .col-md-4
          figure
            img(src='@/assets/curso/51.jpg', alt='Texto que describa la imagen')
      .row(titulo="El diseo grfico en los aos 30").p-3
        .col-md-8.mb-4.mb-md-0
          p La dcada de los aos 30 se caracteriz por dejar atrs los excesos del Art Dco, esto, sumado a la gran depresin de 1928, gener un cambio en el diseo y las artes. Con formas restringidas tomadas del Art Dec y colores tenues, el cartel se vuelve sombro. 
            br
            br
            |Tschichold, tipgrafo alemn, hace grandes aportes a la tipografa, el arte y la arquitectura, con la aplicacin de conceptos constructivistas. En su libro <b>La nueva tipografa</b>, da a conocer tipografas asimtricas y conceptos del diseo asimtrico, que poco se utilizaba en ese momento. 
            br
            br
            |Los avances tecnolgicos y el diseo van a la par, esto se refleja en los trabajos realizados, que buscan dar un falso movimiento a las piezas. Se destacan en esta dcada: Herbert Matter, Piet Zwart, y Lester Beall.
          h4 Caractersticas
          ol.lista-ol--cuadro--color
            li 
              .lista-ol--cuadro__vineta
                span 1
              | Diseos sobrios.
            li 
              .lista-ol--cuadro__vineta
                span 2
              | Colores tenues, poco clidos. 
            li 
              .lista-ol--cuadro__vineta
                span 3
              | Formas no simtricas.
        .col-md-4
          figure
            img(src='@/assets/curso/52.jpg', alt='Texto que describa la imagen')
      .row(titulo="El diseo durante la II Guerra Mundial (cartel blico)").p-3
        .col-md-8.mb-4.mb-md-0
          p En 1939, comienza la Segunda Guerra Mundial, conflicto que durar hasta el ao 1945, dividiendo al mundo entre el bloque occidental y el comunista.  La nocin de la guerra y las revoluciones hace que el diseo sea un arma poltica, capaz de transmitir un mensaje claro y efectivo, ya que es utilizado por los gobiernos para impulsar sus ideologas, infundir miedo y como propaganda poltica.
            br
            br
            |Se utilizan colores de gamas grisceas y en otros colores primarios, sobre todo para exaltar al Gobierno y el servicio a la patria, al cual estaban destinados los hombres.
            br
            br
            |En Estados Unidos, sobresale el trabajo de James Montgomery Flagg, del famoso cartel del <b>to Sam</b>; J. Howard Miller, creador del cartel <b>Podemos hacerlo</b>!, que se muestra en la imagen; y en Alemania, Joseph Goebbels, con un cartel icnico citando <b>la victoria ser nuestra</b>, el cual muestra un soldado frente a la bandera del partido Nazi.
          h4 Caractersticas
          ol.lista-ol--cuadro--color
            li 
              .lista-ol--cuadro__vineta
                span 1
              | Uso poltico.
            li 
              .lista-ol--cuadro__vineta
                span 2
              | Se utiliza la manipulacin mediante el cartel.
            li 
              .lista-ol--cuadro__vineta
                span 3
              | Utilizacin de colores primarios y neutros. 
        .col-md-4
          figure
            img(src='@/assets/curso/podemos.png', alt='Texto que describa la imagen')
    #t_1_4.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.4 Diseo bsico
    .row.mb-5.justify-content-center.align-items-center
      .col-lg-7(data-aos="fade-left")
        p El diseo visual, base de la creacin del diseo, es considerado hoy un eje fundamental de la vida cotidiana de los individuos, puesto que se vive en un mundo cada vez ms globalizado, donde existe saturacin por miles de imgenes que a diario se distribuyen a travs de diversos medios. Es as como la comunicacin se logra por medio del lenguaje escrito, verbal y visual, cada uno con un cdigo particular, utilizados en diversos campos y mbitos de la vida cotidiana. Para referirse al lenguaje visual, se puede decir entonces que es la capacidad de comunicacin mediante imgenes, utilizadas como medio de expresin en todas sus formas y posibilidades, para dar a conocer sensaciones, pensamientos, emociones y sentimientos, que se pueden expresar en representaciones grficas. 
        .bg--4-claro.p-4.mb-3
          p.mb-0 Segn el diccionario etimolgico (Etimologas de Chile, 2020), la palabra imagen proviene del latn <i>imago</i>, la cual significa retrato, copia o imitacin. A pesar de esto, la imagen, a diferencia de la escritura o el lenguaje, suele tener un grado de dificultad en cuanto a su lectura, ya que cada individuo puede realizar su propia interpretacin de la misma, lo cual no sucede con las palabras; por ejemplo, al decir perro, la asociacin que generalmente se hace es con un animal de 4 patas, indiferente de su raza.
        p No siempre una imagen vale ms que mil palabras; por ejemplo, esta seal que se expone significa circulacin prohibida, y es probable que pocos asocien la imagen a este concepto.
      .col-lg-5(data-aos="fade-right")
        .imagen-adornos-flotantes
          img(src="@/assets/curso/53.jpg" alt="Adornos flotantes")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float5.png" style="left: ; right: 10% ; top: ; bottom: 10% ; width: 90px ")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float5.png" style="left: 10% ; right: ; top:0 ; bottom: ; width: 60px ")
          .imagen-adornos-flotantes__img: img(src="@/assets/curso/float3.png" style="left: 15% ; right: ; top: ; bottom: 5% ; width: 60px ")
    .tarjeta.bg--2-claro.p-4.mb-5.rounded-0.mt-5
          .row.justify-content-around.align-items-center.position-relative
