    h3(data-aos="fade-right") Herramientas del lenguaje visual
    p.mb-5 El lenguaje visual se organiza a partir de dos tipos de herramientas
    h4(data-aos="fade-right") A. Herramienta de configuracin: 
    p.mb-5 Dentro de las herramientas de configuracin, se pueden resaltar las siguientes: tamao, forma, color, textura e iluminacin; detllelas a continuacin.
    .row.mb-5
      .col-sm-6.col-xl-3.mb-4.mb-xl-0
        .tarjeta.tarjeta-slide.arriba.color-primario(@mouseover="indicadorTarjetaSlide = false" style="height: 427px ")
          .indicador--hover(v-if="indicadorTarjetaSlide")
          .tarjeta-slide__contenedor
            .tarjeta-slide__contenido.py-4.p-xl-5.px-2
              img(src="@/assets/curso/59.svg" alt="Adornos flotantes" style="width: 70px ").m-auto.mb-3
              .text-center.text-small
                h3 Tamao
                p  Hace referencia a la dimensin adecuada con la cual trabajar una imagen de acuerdo con los criterios requeridos, para conseguir los mejores resultados.
            .tarjeta-slide__img(:style="{'background-image': `url(${require('@/assets/curso/55.jpg')})`}")
      .col-sm-6.col-xl-3.mb-4.mb-xl-0
        .tarjeta.tarjeta-slide.arriba.color-secundario(@mouseover="indicadorTarjetaSlide = false" style="height: 427px ")
          .indicador--hover(v-if="indicadorTarjetaSlide")
          .tarjeta-slide__contenedor
            .tarjeta-slide__contenido.py-4.p-xl-5.px-2
              img(src="@/assets/curso/60.svg" alt="Adornos flotantes" style="width: 70px ").m-auto.mb-3
              .text-center.text-small
                h3 Forma
                p  Todo aquello que nos rodea tiene una forma, se podra definir como el aspecto exterior de las cosas, que presentan un contorno, textura, tamao y color. 
            .tarjeta-slide__img(:style="{'background-image': `url(${require('@/assets/curso/56.jpg')})`}")
      .col-sm-6.col-xl-3.mb-4.mb-xl-0
        .tarjeta.tarjeta-slide.arriba.color-primario(@mouseover="indicadorTarjetaSlide = false" style="height: 427px ")
          .indicador--hover(v-if="indicadorTarjetaSlide")
          .tarjeta-slide__contenedor
            .tarjeta-slide__contenido.py-4.p-xl-5.px-2
              img(src="@/assets/curso/61.svg" alt="Adornos flotantes" style="width: 70px ").m-auto.mb-3
              .text-center.text-small
                h3 Color
                p  El lenguaje visual tiene como gran recurso la utilizacin del color, el cual puede aportar mucha fuerza, carcter e informacin que enriquece la imagen. Mediante el color, se pueden afianzar los conceptos fro, calor, primavera, invierno, entre otros. De igual manera, ofrece una gama ilimitada de posibilidades en su tratamiento. 
            .tarjeta-slide__img(:style="{'background-image': `url(${require('@/assets/curso/57.jpg')})`}")
      .col-sm-6.col-xl-3.mb-4.mb-xl-0
        .tarjeta.tarjeta-slide.arriba.color-secundario(@mouseover="indicadorTarjetaSlide = false" style="height: 427px ")
          .indicador--hover(v-if="indicadorTarjetaSlide")
          .tarjeta-slide__contenedor
            .tarjeta-slide__contenido.py-4.p-xl-5.px-2
              img(src="@/assets/curso/62.svg" alt="Adornos flotantes" style="width: 70px ").m-auto.mb-3
              .text-center.text-small
                h3 Textura
                p  Toda figura tiene una superficie, esta puede tener diferentes caractersticas, unas suaves, otras speras, arrugadas, lisas o abultadas. Existen la textura visual y la textura tctil.
            .tarjeta-slide__img(:style="{'background-image': `url(${require('@/assets/curso/58.jpg')})`}")
    h4(data-aos="fade-right") B. Herramientas de organizacin: 
    p.mb-5 Dentro de estas, se encuentran la composicin y la retrica visual. Es necesario conocer ms sobre ellas, por lo tanto, observe lo siguiente:
    .row.mb-5
      .col-lg-6(data-aos="fade-right")
        .tarjeta.bg--3-claro.p-5.h-100
          img(src="@/assets/curso/63.svg" alt="Adornos flotantes" style="width: 70px ").mb-3
          h5 La composicin: 
          p.mb-3 La composicin de una imagen se hace ordenando las herramientas y elementos que integran la obra en pro de la intencionalidad y el mensaje a transmitir.
          img(src="@/assets/curso/65.jpg" alt="Adornos flotantes" ).m-auto.mb-3 
      .col-lg-6(data-aos="fade-right")
        .tarjeta.bg--4-claro.p-5.h-100
          img(src="@/assets/curso/64.svg" alt="Adornos flotantes" style="width: 70px ").mb-3
          h5 La retrica visual:
          p.mb-3 Reconecta los significados de los componentes del producto visual, para transmitir mensajes y sensaciones ms all del sentido literal.  Los tres componentes de la retrica visual son:
          .bg-white.p-3
            #carouselExampleIndicators.carousel.slide(data-bs-ride='carousel').px-5
              .carousel-indicators
                button.active(type='button' data-bs-target='#carouselExampleIndicators' data-bs-slide-to='0' aria-current='true' aria-label='Slide 1')
                button(type='button' data-bs-target='#carouselExampleIndicators' data-bs-slide-to='1' aria-label='Slide 2')
                button(type='button' data-bs-target='#carouselExampleIndicators' data-bs-slide-to='2' aria-label='Slide 3')
              .carousel-inner
                .carousel-item.active
                  .row.align-items-center
                    .col-lg-3: img(src="@/assets/curso/66.svg")
                    .col-lg-9
                      h4 Metfora:
                      p Comparacin implcita de dos trminos a partir de la semejanza de sus significados, estableciendo una relacin. Ejemplo, el sueo eterno, para referirse a la muerte.

                .carousel-item
                  .row.align-items-center
                    .col-lg-3: img(src="@/assets/curso/67.svg")
                    .col-lg-9
                      h4 Alegora:
                      p Metfora extendida o sucesin de metforas para expresar algo diferente a lo dicho. Ejemplo: Ella gira dentro de mi cabeza.
                .carousel-item
                  .row.align-items-center
                    .col-lg-3: img(src="@/assets/curso/68.svg")
                    .col-lg-9
                      h4 Metonimia:
                      p Reemplazar el nombre de un elemento por otro con el que se relaciona o existe una relacin de dependencia causa  efecto. Ejemplo: le hizo dao el sol, para referirse al calor del sol. 
              .slyder-1
              button.carousel-control-prev(type='button' data-bs-target='#carouselExampleIndicators' data-bs-slide='prev')
                span.carousel-control-prev-icon(aria-hidden='true')
                span.visually-hidden Previous
              button.carousel-control-next(type='button' data-bs-target='#carouselExampleIndicators' data-bs-slide='next')
                span.carousel-control-next-icon(aria-hidden='true')
                span.visually-hidden Next
    h3(data-aos="fade-right") Gramtica del lenguaje visual
    p.mb-5 As como el lenguaje escrito necesita de un abecedario, signos y smbolos para poder llegar a manifestar de forma asertiva un mensaje, el lenguaje visual tambin necesita de ciertos recursos grficos para comunicar de manera correcta aquello que desea expresar, ordenando para ello los elementos que hacen parte de la comunicacin grfica, desde el punto y la lnea. No obstante, no se puede negar que el proceso visual es una sntesis de conocimientos perceptivos que acompaan al individuo, por lo que su lectura, respecto a la imagen, es individual. <b>En resumen, la gramtica visual ordena los elementos que hacen parte de s misma, los procesos que lleva a cabo y las relaciones que se producen.</b>
    .tarjeta.bg--2-claro.p-4.mb-5.rounded-0.mt-5
          .row.justify-content-around.align-items-center.position-relative
            img.position-absolute.d-none.d-lg-block(src="@/assets/curso/54.svg" alt="Adornos flotantes" style="width: 117px ; left: 3% ; top: -80% ")
            .col-4.col-sm-2.col-lg-1
            .col-lg-10
              .row.justify-content-between.align-items-center
                .col.mb-3.mb-sm-0
                  p.text-small.mb-0 Para  reforzar sus conceptos sobre este tema, se le invita a ver el video <b>Conceptos de diseo: la forma</b>, que se encuentra en el material complementario.
                .col-sm-auto
                  a.boton.color-acento-contenido.tb(href='https://www.youtube.com/watch?v=dstsb4-2fCc' target='_blank')
                    span Enlace
                    i.fas.fa-link
    h4(data-aos="fade-right") Forma, repeticin, estructura, contraste y textura
    p.mb-5 Cuando se habla de elementos del diseo, se dice que existen:
    .row.mb-5
      .col-lg-6(data-aos="fade-right")
        .tarjeta.tarjetas-text
          img(src="@/assets/curso/69.jpg" alt="Adornos flotantes" style="width: 100% ").mb-3
          .tarjetas-text__contenido.p-5
            h3 Elementos conceptuales
            p Son aquellos elementos que, aunque no son visibles, estn en la mente por asociacin; por ejemplo, cuando dibuja un cubo por medio de lneas, implcitamente le da una tridimensionalidad que no es real, pero que se forma como reflejo de la imagen y la asociacin.
      .col-lg-6(data-aos="fade-left")
        .tarjeta.tarjetas-text
          img(src="@/assets/curso/70.jpg" alt="Adornos flotantes" style="width: 100% ").mb-3
          .tarjetas-text__contenido.p-5
            h3 Elementos visuales
            p Hacen destacar las caractersticas de una imagen, como su color, textura, ancho y alto. Un concepto muy utilizado dentro del diseo es el mdulo, elemento tomado como unidad de medida utilizada dentro de una composicin grfica, la cual suele repetirse sistemticamente. 
    p.mb-5 Se le invita a revisar la siguiente presentacin, que le ayudar a profundizar sobre el tema:
    .tarjeta.tarjeta--azul.p-4.mb-5
      SlyderA.p-5
        .row
          .col-md-7.mb-4.mb-md-0
            h4 Forma
            p Los elementos conceptuales no son visibles, as el punto, la lnea o el plano, cuando son visibles, pasan a ser una forma. Un punto sobre el papel, por pequeo que sea, debe tener una figura, un tamao, un color y una textura si se pretende que sea visto. Tambin debe sealarse lo mismo de una lnea o de un plano. En un diseo bidimensional, el volumen es imaginario.
              br
              br
              |Dentro de la forma encontramos:
            .row.justify-content-center
              .col-auto(@mouseover="mostrarIndicador = false").mb-3
                a.boton.color-acento-botones.indicador__container(@click="modal1 = true")
                  span.fw-bold.text-white La forma como punto
                  .indicador--click(v-if="mostrarIndicador")
              .col-auto.mb-3
                a.boton.color-acento-botones.indicador__container(@click="modal2 = true")
                  span.fw-bold.text-white La forma como lnea
              .col-auto.mb-3
                a.boton.color-acento-botones.indicador__container(@click="modal3 = true")
                  span.fw-bold.text-white La forma como plano
          .col-md-5
            figure
              img(src='@/assets/curso/71.jpg', alt='Texto que describa la imagen')
        .row
          .col-md-7.mb-4.mb-md-0
            h4 Repeticin
            p La repeticin tiene como funcin unificar el diseo, se da como resultado de la repeticin continua de un mismo elemento dentro de un espacio, lo cual permite igualdad, ritmo y equilibrio a nivel visual.
          .col-md-5
            figure
              img(src='@/assets/curso/72.jpg', alt='Texto que describa la imagen')
        .row
          .col-md-7.mb-4.mb-md-0
            h4 Estructura
            p La estructura sugiere un orden y predefine las relaciones internas de las formas en un diseo, es decir la posicin. Es posible haber creado un diseo sin haber pensado conscientemente en la estructura, pero la estructura est siempre presente cuando hay una organizacin (Wong, 1991). 
          .col-md-5
            figure
              img(src='@/assets/curso/73.svg', alt='Texto que describa la imagen')
        .row
          .col-md-7.mb-4.mb-md-0
            h4 Radiacin
            p Puede ser descrita como un caso especial de repeticin. Se trata de los mdulos repetidos o las subdivisiones estructurales que giran regularmente alrededor de un centro comn, produciendo un efecto de radiacin. La radiacin es un fenmeno comn en la naturaleza; por ejemplo, lanzar una piedra a un lago genera ondas concntricas, provocando una gradacin, serie ordenada gradualmente (Godoy, 2009b).
          .col-md-5
            figure
              img(src='@/assets/curso/74.svg', alt='Texto que describa la imagen')
        .row
          .col-md-7.mb-4.mb-md-0
            h4 Contraste
            p El contraste ocurre siempre, aunque su presencia pueda no ser advertida. Se presenta el contraste cuando una forma est rodeada de un espacio blanco; cuando una lnea recta se cruza con una curva; cuando una forma es mucho mayor que otra; cuando coexisten direcciones verticales y horizontales. El contraste es solo una clase de comparacin, por la cual las diferencias se hacen claras.
              br
              br
              | Dos formas pueden ser similares en algunos aspectos y diferentes en otros. Sus diferencias quedan enfatizadas cuando hay un contraste. Una forma puede no parecer grande si es vista de forma aislada, pero puede parecer gigantesca junto a formas diminutas.
          .col-md-5
            figure
              img(src='@/assets/curso/75.svg', alt='Texto que describa la imagen')
        .row
          .col-md-7.mb-4.mb-md-0
            h4 Contraste
            p La regularidad no produce necesariamente un buen diseo, aunque puede garantizar cierto grado de armona. El mismo grupo de mdulos, utilizados en una estructura de repeticin, pueden derivar en un diseo opaco en las manos de un diseador y en un diseo llamativo en manos de otro. El debido uso del contraste en los elementos de relacin puede explicar esa diferencia.  El contraste puede ser derivado de elementos como la figura, el tamao, el color, la textura, la direccin de los elementos, la posicin, el espacio y la gravedad (Godoy, 2009a). 
          .col-md-5
            figure
              img(src='@/assets/curso/76.svg', alt='Texto que describa la imagen')
        .row
          .col-md-7.mb-4.mb-md-0
            h4 Textura
            p De acuerdo con Wong (1991), la textura se refiere a las caractersticas de una superficie de una figura. Toda figura tiene una superficie y toda superficie debe tener ciertas caractersticas, que pueden ser descritas como suave, rugosa, lisa o decorada, opaca o brillante, blanda o dura. Aunque, generalmente, se supone que una superficie plana y pintada no tiene textura alguna, en realidad, la capa de pintura es ya una textura; como, tambin, la textura del material sobre el que fue creada la figura. La textura apropiada aade riqueza a un diseo. 
              br
              br
              |La textura puede ser clasificada en dos importantes categoras: textura visual y textura tctil.
            .row.justify-content-center
              .col-auto(@mouseover="mostrarIndicador = false")
                a.boton.color-acento-botones.indicador__container(@click="modal4 = true")
                  span.fw-bold.text-white Textura visual
                  .indicador--click(v-if="mostrarIndicador")
              .col-auto
                a.boton.color-acento-botones.indicador__container(@click="modal5 = true")
                  span.fw-bold.text-white Textura tctil
          .col-md-5
            figure
              img(src='@/assets/curso/77.jpg', alt='Texto que describa la imagen')
    h4(data-aos="fade-right") Composicin, percepcin y equilibrio visual
    p.mb-5 En diseo, la composicin es el ordenamiento y disposicin de los distintos elementos dentro del espacio visual de manera ordenada, con el fin de transmitir un mensaje objetivo al pblico. El xito de cualquier proyecto grfico reside en la composicin, que sea clara, concisa, sencilla y directa, esto posibilita una mejor lectura de esta.
      br
      br
