            .tarjeta-c.bg-white.p-3(style='bottom: 10%; left: 85px')
              p La percepcin se define como la capacidad de construir, interpretar y hasta corregir un esquema de informacin recibido por medio de los rganos sensoriales. Suele estar condicionada a patrones o aprendizajes memorizados con anterioridad. En el caso de la percepcin visual, es la capacidad de procesar e interpretar la informacin recibida por medio del ojo como rgano principal; se hace uso de ciertos principios de la percepcin, basados en las leyes de la Gestalt, teora de la psicologa que permite estudiar los modos de cmo se percibe lo que vemos.
        .carousel-item
          div.position-relative
            img(src='@/assets/curso/92.jpg', alt='Texto que describa la imagen')
            .tarjeta-c.bg-white.p-3(style='bottom: 10%; left: 85px')
              h5 Diseo bidimensional
              p Un elemento bidimensional es aquel que representa su altura y anchura sin manifestar su profundidad, ya que se realiza en un espacio plano, el cual solo tiene estas 2 dimensiones. <b>Ejemplo de esto, un dibujo de un rectngulo en una hoja de papel</b>. 
                br
                br
                |Es as como, al referirse al diseo bidimensional, se est hablando de aquel diseo que puede ser expresado por medio de dibujos, planos, ilustraciones, tipografas, fotografas, imgenes, etc. Se encuentra en diversas corrientes, como las artes visuales, el diseo grfico, el arte, la pintura, el grafiti, entre otros. A diferencia del diseo tridimensional, slo puede ser observado de manera frontal, un ejemplo claro de esto se da al mirar una revista, solo se aprecia lo que est frente a los ojos; situacin contraria cuando se observa una escultura en un museo, tiene muchos puntos de vista. 
                br
                br
                |En pocas palabras, el diseo es la mejor expresin visual de la esencia de algo (Wong,1991).
        .carousel-item
          div.position-relative
            img(src='@/assets/curso/93.jpg', alt='Texto que describa la imagen')
            .tarjeta-c.bg-white.p-3(style='bottom: 10%; left: 85px')
              h5 Diseo tridimensional
              p Un elemento tridimensional posee un volumen dado por altura, anchura y profundidad. La percepcin de un elemento tridimensional no puede darse a primera vista, tiene que observarse desde diferentes ngulos, posiciones y distancias, para hacer una mejor lectura de este. As, el diseo tridimensional presenta muchos retos adicionales al bidimensional, precisamente por todas las aristas de observacin. El diseador tiene que ser muy observador e imaginar el elemento en diversas posiciones y tamaos, teniendo gran importancia el volumen y la ubicacin dentro de un espacio; para dicho propsito, se deben asimilar sus medidas de manera transversal, vertical y horizontal.
